import React, { useEffect, useState } from "react";
import Layout from "../components/layout";
import HeaderTwo from "../components/header/header-two";
import { graphql } from 'gatsby'
import Loader from '../template/Card'
import { number_container, Row, Col } from "react-bootstrap";
import { filters } from '../helpers/filter'
import NumberFinder from '../components/NumberFinder'

const HomeTwo = ({ data, location }) => {

  const [pageSize, setPageSize] = useState(40)
  const [currentPage, setCurrentPage] = useState(0)
  const [filterList, setFilterList] = useState([])
  const masterList = data.products.edges.map(({ node }, i) => ({ ...node })).map(e => ({
    number: e.num,
    group: e.type
  }))

  const [query, setQuery] = useState("");
  const [category, setCategory] = useState("All");
  useEffect(() => {
    setFilterList(masterList
      .slice(currentPage * pageSize, currentPage * pageSize + 40)
      .filter(e => filters[category] ? filters[category](e) : false)
      .filter(e => e.number.includes(query)))
  }, [query, category, currentPage])
  const params = new URLSearchParams(location.search);
  const mask = !!params.get("mask");

  const formatNumber = (e) => {
    return e.slice(0, 4) + '-' + e.slice(4)
  }


  const increasePage = () => {
    if (currentPage + 1 < calculatePages()) {
      setCurrentPage(currentPage + 1)
    } else {
      setCurrentPage(0)
    }
  }

  const calculatePages = () => {
    return Math.ceil(masterList.length / pageSize);
  }

  useEffect(() => {
    setTimeout(() => {
      increasePage()
    }, 10000);
  }, [currentPage])
  const startLive = true
  return (
    <>
      <HeaderTwo onSearch={setQuery} onCategory={setCategory} hidePhone={true}/>


      {
        !startLive && <div className="container">
          <div className="number_container">
            <h2 style={{ margin: '20px auto ' }}>Live is Closed. Next Live 29/11/2023 7PM</h2>
          </div>
        </div>
      }
      {
        startLive && <div className="container">
          <div className="number_container">
            <h2 style={{ margin: '0 auto ' }}>SG Mobile Number For Sale Only VIA TIKTOK </h2>
          </div>

          <div className="number_container">
            <h2 style={{ margin: '20px auto ' }}> Get your Lucky Number at $38 Or $88 depend on Number </h2>
          </div>
          <div className="number_container">
            <h2 style={{ margin: '20px auto ' }}> Check Our Profile For Contact Details to Purchase the Number </h2>
          </div>
          <div className="number_container">
            {
              filterList.map(e => <h3 className="number-pick"> {formatNumber(e.number)} </h3>)
            }
          </div>
          <div className="number_container">
            Page {currentPage + 1} of {calculatePages()}
          </div>
        </div>
      }
    </>
  );

};

export default HomeTwo;


export const query = graphql`
      {
        products: allLiveJson {
        edges {
        node {
        id,
        num,
        type
      }
        }
      }
    }
      `
